<template>
	<view class="detail">
		<view class="skin" v-if="loading"></view>
		<h2 class="detail__title">{{detailData.title}}</h2>
		<view class="detail__author">
			<view class="detail__author__head">
				<image :src="author.avatar" mode="aspectFit"></image>
			</view>
			<view class="detail__author__info">
				<view class="detail__author__name">{{author.author_name}}</view>
				<view class="detail__author__tab">
					<view class="detail__author__tab__item">{{detailData.create_time}}</view>
					<view class="detail__author__tab__item">{{detailData.browse_count}} 浏览</view>
					<view class="detail__author__tab__item">{{detailData.collection_count}} 赞</view>
				</view>
			</view>
			<view class="detail__author__btn">{{ detailData.is_author_like ? '已关注' : '关注'}}</view>
		</view>

		<view class="detail__content">
			<view class="detail__content__text">
				<uParse :content="detailData.content" noData="加载中...."></uParse>
			</view>
		</view>
		
		<view class="detail__line"></view>

		<view class="comment-box">
			<view class="comment-box__title">精彩评论</view>
			<view class="comment-box__item" v-for="item in commentList" :key="item._id">
				<detail-comment :is_reply="false" :comment="item" @reply="reply"></detail-comment>
			</view>
			<view v-if="commentList.length <= 0" class="comment-box__null">暂无评论！</view>
		</view>

		<view class="detail__tabbar">
			<view class="detail__tabbar__left" @click="handleOpen">
				<text class="detail__tabbar__left__input">谈谈你的看法</text>
				<text class="iconfont detail__tabbar__left__icon">&#xec88;</text>
			</view>
			<view class="detail__tabbar__right">
				<text class="iconfont detail__tabbar__right__icon">&#xe687;</text>
				<text class="iconfont detail__tabbar__right__icon"
					v-html="detailData.is_article_like ? '&#xe684;' : '&#xe643;'"></text>
				<text class="iconfont detail__tabbar__right__icon"
					v-html="detailData.is_thumbs_up_article ? '&#xec8c;' : '&#xec7f;'"></text>
			</view>
		</view>
		<uni-popup ref="popup" type="bottom" :maskClick="false">
			<view class="popup">
				<view class="popup__header">
					<view @click="handleClose">取消</view>
					<view @click="handleSubmit">发布</view>
				</view>
				<view class="popup__content">
					<textarea class="popup__content__text" v-model="textareaVal" maxlength="200"
						placeholder="请输入评论内容"></textarea>
					<view class="popup__content__count">{{textareaVal.length}}/200</view>
				</view>
			</view>
		</uni-popup>
	</view>
</template>

<script>
	import {
		get_detail,
		set_comment,
		get_comment
	} from '../../common/api'
	import uParse from '../../components/gaoyia-parse/parse.vue'
	export default {
		components: {
			uParse
		},
		data() {
			return {
				detailData: {},
				author: {},
				detail_id: '',
				textareaVal: '',
				commentList: [],
				replyFormData: {},
				loading: true
			}
		},
		onLoad(query) {
			this.detail_id = query.detail_id
			this.getDetail()
			this.getComment()
		},
		methods: {
			reply(e) {
				this.replyFormData = {
					comment_id: e.comment._id,
					is_reply: e.is_reply
				}
				if (e.is_reply) {
					this.replyFormData.reply_id = e.reply_id
				}
				this.handleOpen()
			},
			handleOpen() {
				this.$refs.popup.open()
			},
			handleClose() {
				this.$refs.popup.close()
			},
			handleSubmit() {
				const ask = /^\s+$/g
				if (!this.textareaVal || ask.test(this.textareaVal)) return uni.showToast({
					title: '内容不能为空',
					icon: 'none'
				})
				uni.showLoading()
				const formData = {
					article_id: this.detailData._id,
					content: this.textareaVal,
					...this.replyFormData
				}

				// 提交评论
				set_comment(formData).then(res => {
					uni.hideLoading()
					uni.showToast({
						title: '评论成功'
					})
					this.textareaVal = ''
					this.getComment()
				})
				this.$refs.popup.close()
			},
			getDetail() {
				this.loading = true
				uni.showLoading()
				get_detail({
					detail_id: this.detail_id
				}).then(res => {
					this.detailData = res.data
					this.author = res.data.author
					uni.hideLoading()
					this.loading = false
				})
			},
			getComment() {
				get_comment({
					detail_id: this.detail_id
				}).then(res => {
					this.commentList = res.data
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	.skin {
		z-index: 999;
		position: fixed;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		background-color: #fff;
	}

	.detail {
		background-color: #fff;
		padding: 15px 0 54px;

		&__title {
			padding: 0 15px;
		}

		&__author {
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 15px;
			margin: 10px 0;

			&__head {
				width: 40px;
				height: 40px;
				border-radius: 50%;
				overflow: hidden;

				image {
					width: 100%;
					height: 100%;
				}
			}

			&__info {
				flex: 1;
				padding: 0 10px;
			}

			&__name {
				font-size: 14px;
				color: #333;
				margin-bottom: 6px;
			}

			&__tab {
				display: flex;
				align-items: center;
				font-size: 12px;
				color: #666;

				&__item {
					margin-right: 10px;
				}
			}

			&__btn {
				width: 60px;
				line-height: 27px;
				text-align: center;
				font-size: 14px;
				color: #fff;
				background-color: #ff5e5e;
				border-radius: 5px;
			}
		}

		&__content {
			min-height: 450px;
			margin-top: 10px;

			&__text {
				padding: 0 15px;
			}
		}

		&__tabbar {
			display: flex;
			align-items: center;
			position: fixed;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 44px;
			border-top: 1px solid #f5f5f5;
			background-color: #fff;

			&__left {
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 55%;
				height: 30px;
				border: 1px solid #ddd;
				border-radius: 5px;
				padding: 0 10px;
				margin-left: 15px;

				&__input {
					color: #666;
					font-size: 12px;
				}

				&__icon {
					color: #f07373;
					font-size: 14px;
				}
			}

			&__right {
				flex: 1;
				display: flex;
				justify-content: space-around;

				&__icon {
					font-size: 20px;
					color: #f07373;
				}
			}
		}
		&__line {
			border-bottom: 5px solid #efefef;
			margin: 10px 0;
		}
	}

	.popup {
		width: 100%;
		background-color: #fff;

		&__header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 45px;
			padding: 0 15px;
			font-size: 14px;
			color: #333;
			border-bottom: 1px solid #f5f5f5;
		}

		&__content {
			padding: 15px;

			&__text {
				width: 100%;
				height: 250px;
				color: #333;
			}

			&__count {
				text-align: right;
				font-size: 12px;
				color: #666;
			}
		}
	}

	.comment-box {
		color: #333;
		margin-top: 20px;
		font-size: 14px;

		&__null {
			font-size: 12px;
			color: #666;
			text-align: center;
			padding: 50px;
		}

		&__title {
			position: relative;
			padding: 0 15px;
			margin-bottom: 20px;
			font-weight: bold;
			&::after{
				content: "";
				position: absolute;
				top: 2px;
				bottom: 1px;
				left: 5px;
				width: 4px;
				background-color: #36AB60;
			}
		}
	}
</style>